
*:not(.swiper-wrapper){
  overflow: hidden;
  box-sizing: border-box;
}
html,body{
  margin:0;
  padding:0;
  width:100%;
  height:100%;
  background-color: #333;
}
.container{
  min-height:0.01rem;
  max-width:720px;
  width:100%;
  margin:0 auto;
  left:0;
  right:0;
}
.row{
  width:100%;
  min-height:0.01rem;
}
.contain{
  width: 95%;
  margin:0 auto;
}
a{
  text-decoration: none;
  color:#000;
}
ul li,ul{
  list-style: none;
  margin:0;
  padding:0;
}
.clear:after{
  content: "";
  display:block;
  width: 0;
  height:0;
  clear: both;
}
#app { 
  height:100%;
  font-size:0.2rem;
  position: relative;
  background-color: #fff;
}

