.ts-content {
  border-bottom: 1px solid #eee;
  position: fixed;
  top: 0;
  left: 0;
  height: 0.8rem;
  background-color: #fff;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  /*"搜索"字体大小*/
}
.ts-content > .iconfont {
  flex-grow: 2;
  line-height: 0.8rem;
  text-align: center;
  font-size: 0.35rem;
  color: gray;
}
.ts-content > .search-text-size {
  font-size: 0.25rem;
}
.ts-content > .ts-search {
  flex-grow: 6;
  height: 0.8rem;
  display: flex;
  align-items: center;
  /*选中输入框样式*/
}
.ts-content > .ts-search > .iconfont {
  flex-grow: 1;
  height: 0.5rem;
  line-height: 0.5rem;
  color: grey;
  font-size: 0.35rem;
  text-align: center;
  background-color: #eeeeee;
  border-radius: 5px 0 0 5px;
}
.ts-content > .ts-search > .ts-input {
  flex-grow: 9;
  outline: none;
  height: 0.5rem;
  border: none;
  border-radius: 0px 5px 5px 0px;
  background-color: #eeeeee;
}
.ts-content > .ts-search > .ts-inputFocus {
  text-indent: 0.2rem;
  border-radius: 5px;
  border: 1px solid #FC5779;
}
.ts-content > .ts-search > .ts-inputBlur {
  border: none;
}
/*搜索提示页*/
.ts-searchPage {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #fff;
  top: 0.8rem;
  color: #aaa;
}
.ts-searchPage > .ts-searchHistory {
  padding: 0.3rem 0.2rem;
  border-bottom: 1px solid #eee;
}
.ts-searchPage > .ts-searchHistory > .ts-searchTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.25rem;
}
.ts-searchPage > .ts-searchHistory > .ts-noHistory {
  padding: 0.2rem 0 0 0.3rem;
}
.ts-searchPage > .ts-searchHistory > ul {
  padding: 0.2rem 0 0 0;
}
.ts-searchPage > .ts-searchHistory > ul > .ts-searchHistoryPill {
  color: #555;
  display: inline-block;
  padding: 0.05rem 0.1rem;
  margin: 0.05rem 0.2rem;
  border: 1px solid #eee;
  border-radius: 0.05rem;
}
.ts-searchPage > .ts-hotword {
  padding: 0.3rem 0.2rem;
}
.ts-searchPage > .ts-hotword > p {
  font-size: 0.25rem;
}
.ts-searchPage > .ts-hotword > .ts-hotwordShow > span {
  color: #555;
  display: inline-block;
  padding: 0.05rem 0.2rem;
  margin: 0.2rem 0.2rem;
  border: 1px solid #eee;
  border-radius: 0.05rem;
}
/*搜索提示页是否显示*/
.ts-searchPage-show {
  display: none;
}

